<template>
  <div class="exchange-body">
    <div class="btn-row">
      <div class="title-btn" @click="titleClick(0)" :class="{'on':titleBtn===0}">买入</div>
      <div class="title-btn" @click="titleClick(1)" :class="{'on':titleBtn===1}">卖出</div>
    </div>
    <div v-if="titleBtn ===0">
      <div class="exchange-box">
        <div class="exchange-title-row">
          <span class="lt exchange-title">收款账户</span>
          <span class="gt">{{account.name}}</span>
        </div>
        <div class="exchange-title">兑换币种</div>
        <el-select
          class="input"
          v-model="tokenname"
          placeholder="请选择"
          @change="changeToken"
        >
          <el-option
            v-for="item in tokenarr"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div class="exchange-box" v-if="addressState === 2">
          <qrcode-vue class="qrcode" :value="address" :size="150" level="H"></qrcode-vue>
          <div class="address-font"><span id="address">{{address}}</span><span class="copy-btn" @click="copystr(address)">复制</span></div>
      </div>
      <div class="exchange-box" v-if="addressState === 1">
        <div class="address-waiting">请求中</div>
      </div>
      <div class="exchange-box" v-if="addressState === 0" @click="getaddress()">
        <div class="address-waiting" >获取地址</div>
      </div>

      <div class="exchange-box">
        <div class="order-header">历史订单</div>
        <div class="order-item buy">
          <el-row class="order-item-rol" v-for="item in coinOrder">
            <el-col class="order-name" :span="12">{{item.user}}</el-col>
            <el-col class="order-status finish" :span="12">已完成</el-col>
            <el-col class="order-price" :span="24">+{{item.amount}}</el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div v-if="titleBtn===1">
      <div class="exchange-box">
        <div class="exchange-title-row">
          <span class="lt">付款账户</span>
          <span class="gt">{{account.name}}</span>
        </div>
        <div class="">兑换币种</div>
        <el-select
          class="input"
          v-model="coinname"
          placeholder="请选择"
          @change="coinChange(coinname)"
        >
          <el-option
            v-for="item in coinarr"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <div>兑换数额</div>
        <el-input class="input" type="number" v-model="coinnum"></el-input>
        <div>收款账户</div>
        <el-input class="input" v-model="coinaddress"></el-input>
        <div class="money-row">
          余额：{{currentEOS}}
        </div>
        <el-button class="ex-button" @click="doAction()">提交</el-button>
      </div>
      <div class="order-box">
        <div class="order-header">
          <div class="order-button" :class="{'on':orderBtn===0}" @click="orderClick(0)">处理中</div>
          <div class="order-button" :class="{'on':orderBtn===1}" @click="orderClick(1)">已完成</div>
        </div>
        <div class="order-item" v-if="orderBtn===0">
          <el-row class="order-item-rol" v-for="item in tokenRun">
            <el-col class="order-name" :span="12">{{item.from_user}}</el-col>
            <el-col class="order-status run" :span="12">进行中</el-col>
            <el-col class="order-title" :span="12">兑换金额</el-col>
            <el-col class="order-price" :span="12">{{item.amount}}</el-col>
            <el-col class="order-title" :span="6">转入地址:</el-col>
            <el-col class="order-address" :span="24">{{item.to_address}}</el-col>
          </el-row>
        </div>
        <div class="order-item" v-if="orderBtn===1">
          <el-row class="order-item-rol" v-for="item in tokenFinish">
            <el-col class="order-name" :span="12">{{item.from_user}}</el-col>
            <el-col class="order-status finish" :span="12">已完成</el-col>
            <el-col class="order-title" :span="12">兑换金额</el-col>
            <el-col class="order-price" :span="12">{{item.amount}}</el-col>
            <el-col class="order-title" :span="6">转入地址:</el-col>
            <el-col class="order-address" :span="24">{{item.to_address}}</el-col>
          </el-row>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
  import Eos from 'eosjs';
  import eventHub from '@/utils/event';
  import network from '@/utils/network';
  import api from '@/utils/eos';
  import config from '@/utils/config';
  import createHash from 'create-hash';
  import QrcodeVue from 'qrcode.vue';
  export default {
    mounted () {
      // this.shareBtn()
      var that = this
      api.getInfo({}).then(result => {console.log(result)})
      this.$i18n.locale = this.$lan()
      this.language = this.$lan()
      this.getCoinOrder()
      this.$nextTick(function () {
        if (typeof (scatter) !== 'undefined') {
          if (scatter.identity) {
            const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
            that.$store.commit('UPDATE_ACCOUNT', account);
            that.searchAddress()
          }
          if (!that.$store.state.account || !that.$store.state.account.name) {
            that.login()
            setTimeout(() => {
              that.login()
            }, 5000)
          }
        } else {
          document.addEventListener('scatterLoaded', function () {
            if (scatter.identity) {
              const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
              that.$store.commit('UPDATE_ACCOUNT', account);
              that.searchAddress()
            }
            if (!that.$store.state.account || !that.$store.state.account.name) {
              that.login()
              setTimeout(() => {
                that.login()
              }, 5000)
            }
          })
        }
      })
    },

    data () {
      return {
        address:null,
        num: 0,
        titleBtn:0,
        coinnum:0,
        orderBtn:0,
        addressState:0,
        coinaddress:null,
        coinname: 'BTC',
        coinarr: [{
          value: 'BTC',
          label: 'BTC'
        }
        // , {
        //   value: 'USD',
        //   label: 'USD'
        // }, {
        //     value: 'ETH',
        //     label: 'ETH'
        //   }
          ],
        tokenname: 'EBTC',
        tokenarr: [{
          value: 'EBTC',
          label: 'EBTC'
        }
        // , {
        //   value: 'EUSD',
        //   label: 'EUSD'
        // },{
        //   value: 'EETH',
        //   label: 'EETH'
        // }
        ],
        token:{
          EBTC:0,
          EETH:0,
          EUSD:0,
          EOS:0
        },
        currentEOS:0,
        coinOrder:[],
        tokenRun:[],
        tokenFinish:[]
      };
    },
    methods: {
      getClientSeed () {
        if (this.customSeed) {
          return createHash('sha1').update(this.account.name + this.customSeed).digest('hex');
        } else {
          let randomNumber = Math.floor(Math.random() * Math.floor(Number.MAX_SAFE_INTEGER));
          return createHash('sha1').update(this.account.name + Date.now() + randomNumber).digest('hex');
        }
      },
      login () {
        if (scatter) {
          scatter.getIdentity({
            accounts: [network]
          }).then(() => {
            const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
            if (!account) return;
            this.$store.commit('UPDATE_ACCOUNT', account);
            this.getCoinOrder()
            this.getToken()
            this.searchAddress()
            this.$message({
              showClose: true,
              message: this.$t('login_success'),
              type: 'success',
              duration: 1500
            });
          }).catch(e => {
            this.$message({
              showClose: true,
              message: e.message,
              type: 'warning',
              duration: 1500
            });
          });
        }
      },
      getaddressresult(){
        api.getTableRows({
          "scope": this.account.name,
          "code": config.diceAccount,
          "table": "accounts",
          "json": "true"
        }).then((results) => {
          if(results&&results.rows[0]&&results.rows[0].address){
            this.address = results.rows[0].address
            this.addressState = 2
          }else {
            setTimeout(()=>{
              this.getaddressresult()
            },3000)
          }

        }).catch(e => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      },
      getaddress () {
        const api1 = scatter.eos(network, Eos, {});
        this.addressState = 1
        api1.transaction(
          {
            actions: [
              {
                account: config.diceAccount,  //合约名
                name: 'newaddress',
                authorization: [{
                  actor: this.account.name,
                  permission: 'active',
                }],
                data: {
                  user: this.account.name,
                  symbol: 'EBTC'
                }
              }
            ]
          }
        ).then(result => {
          console.log(result)
          this.addressState = 1
          this.getaddressresult()
        }).catch(e => {
          this.addressState = 0
          var errorstr = typeof e === 'string'?JSON.parse(e):e
          if(errorstr.error.details[0].message = 'assertion failure with message: already has exchange address'){
            this.getaddressresult()
          }

        })

      },
      getToken(){
        if(!this.account.name){
          return
        }
        api.getCurrencyBalance({
          json: true,
          code: 'bitpietokens',
          account: this.account.name
        }).then((results)=> {
          console.log(results)
          if(results&&results.length!=0){
            for(var i=0;i<results.length;i++){
              var str = results[i].split(" ");
              if(str[1]==='EBTC'){
                this.token.EBTC = Number(str[0]).toFixed(6)
              }else if(str[1]==='EETH'){
                this.token.EETH = Number(str[0]).toFixed(4)
              }else if(str[1]==='EUSD'){
                this.token.EUSD = Number(str[0]).toFixed(2)
              }
            }
            console.log(this.token)
            if(this.coinname === 'BTC'){
              this.currentEOS = this.token.EBTC
            }else if(this.coinname === 'ETH'){
              this.currentEOS = this.token.EETH
            }else if(this.coinname === 'USD'){
              this.currentEOS = this.token.EUSD
            }
          }

        })
      },
      copystr (str) {
        // var oInput = document.createElement('input');
        // oInput.value = str;
        // document.body.appendChild(oInput);
        // oInput.select(); // 选择对象
        // document.execCommand("Copy"); // 执行浏览器复制命令
        // oInput.className = 'oInput';
        // oInput.style.display='none';
        // alert('复制成功');

        const range = document.createRange();
        range.selectNode(document.getElementById('address'));

        const selection = window.getSelection();
        if(selection.rangeCount > 0) selection.removeAllRanges();
        selection.addRange(range);
        document.execCommand('copy');
        this.$message({
          showClose: true,
          message: this.$t('copy_success'),
          type: 'success',
          duration: 1500
        });
      },
      titleClick(num){
        this.getToken()
        this.titleBtn = num
        if(num === 0){
          this.searchAddress()
        }
        if(num===1){
          if(this.orderBtn===0){
            this.getTokenRun()
          }else if(this.orderBtn===1){
            this.getTokenFinish()
          }
        }
      },
      changeToken(){
        this.address = null
        this.addressState = 1
        this.searchAddress()
      },
      doAction(){
        var address = this.coinaddress
        var money = this.coinnum
        var coinname = this.coinname
        var valid = WAValidator.validate(address, 'bitcoin');
        if(!valid){
          this.$notify.error('btc地址不合法');
          return false
        }
        if(money>this.currentEOS){
          this.$message({
            showClose: true,
            message:'您的余额不足，请重置',
            type: 'success',
            duration: 1500
          });
          return
        }else if(money==0){
          this.$message({
            showClose: true,
            message: '金额不能为0',
            type: 'success',
            duration: 1500
          });
          return
        }
        if(!address) {
          this.$message({
            showClose: true,
            message: '收币地址不能为空',
            type: 'success',
            duration: 1500
          });
          return
        }
        money = Number(money).toFixed(8)
        const eos = scatter.eos(network, Eos, {})
        eos.transaction({
          actions: [{
            account: 'bitpietokens',//合约名，
            name: "transfer",//
            authorization: [{
              actor: this.account.name,
              permission: this.account.authority
            }],
            data: {
              from: this.account.name,
              to: config.diceAccount,
              quantity:money + ' E'+ coinname,
              memo: address
            }
          }]
        }).then((results) =>{
          this.getToken()
          console.log(results)
        }).catch(e=>{
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      },
      coinChange() {
        this.getToken()
      },
      getCoinOrder(){
        api.getTableRows({
          "scope": config.diceAccount,
          "code": config.diceAccount,
          "table": "buyorders",
          "lower_bound":this.account.name,
          "json": "true"
        }).then((results) => {
          if(this.account.name) {
            if(results.rows&&results.rows[0]) {
              var data = []
              for(var i = 0;i <results.rows.length; i++) {
                if(results.rows[i].user == this.account.name) {
                  data.push(results.rows[i])
                }
              }
              this.coinOrder = data
            }
          }else {
            return false
          }
        }).catch((e) => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      },
      getTokenRun(){
        api.getTableRows({
          "scope": config.diceAccount,
          "code": config.diceAccount,
          "table": "sellorders",
          "json": "true",
          "lower_bound":this.account.name,
        }).then((results) => {
          console.log(results)
          if(this.account.name) {
            if(results.rows&&results.rows[0]) {
              var data = []
              for(var i = 0;i <results.rows.length; i++) {
                if(results.rows[i].from_user == this.account.name) {
                  data.push(results.rows[i])
                }
              }
              this.tokenRun = data
            }
          }else {
            return false
          }
        }).catch((e) => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      },
      getTokenFinish(){
        api.getTableRows({
          "scope": config.diceAccount,
          "code": config.diceAccount,
          "table": "soldorders",
          "json": "true",
          "lower_bound":this.account.name
        }).then((results) => {
          console.log(results)
          if(this.account.name) {
            if(results.rows&&results.rows[0]) {
              var data = []
              for(var i = 0;i <results.rows.length; i++) {
                if(results.rows[i].from_user == this.account.name) {
                  data.push(results.rows[i])
                }
              }
              this.tokenFinish = data
            }
          }else {
            return false
          }
        }).catch((e) => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      },
      orderClick(num){
        this.orderBtn=num
        if(num===0){
          this.getTokenRun()
        }else if(num===1){
          this.getTokenFinish()
        }
      },
      addressTable (){
        api.getTableRows({
          "scope": this.account.name,
          "code": config.diceAccount,
          "table": "newaddresses",
          "json": "true"
        }).then((results) => {
          if(results&&results.rows[0]&&results.rows[0].address){
            this.address = results.rows[0].address
            this.addressState = 1
            this.getaddressresult()
          }else {
            this.addressState = 0
          }

        }).catch((e) => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      },
      searchAddress (){
        api.getTableRows({
          "scope": this.account.name,
          "code": config.diceAccount,
          "table": "accounts",
          "json": "true"
        }).then((results) => {
          console.log(results)
          var a = true
          if(results&&results.rows[0]&&results.rows[0].address){
            for(var i = 0; i<results.rows.length; i++){
              if(results.rows[i].symbol == this.tokenname){
                this.address = results.rows[i].address
                this.addressState = 2
                a = false
              }
            }
          }
          if(a){
            this.addressTable ()
          }

        }).catch((e) => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      }
    },

    watch: {
      account: function () {
        this.getCoinOrder()
      }
    },
    computed: {
      account () {
        return this.$store.state.account;
      }
    },
    components: {
      QrcodeVue
    }
  };
</script>

